import React, { useState } from 'react';
import { IOTEntryProps } from './interface';
import './style/index.less';
const prefixCls = 'iot-entry';
import NormalIcon from '../../base/normalIcon';

const IOTEntry: React.FC<IOTEntryProps> = ({
  streamId,
  subTitle,
  title,
  curValue,
  disabled = false,
  icon,
  serviceName,
  serviceAddress,
  handler = (val: any) => {},
  ...rest
}) => {
  const getIcon = () => {
    if (icon?.show) {
      return <NormalIcon url={icon?.value ? icon.value : 'insideicon-cu-1'}/>;
    }
    return undefined;
  };

  const getContent = () => {
    return (
      <>
        {getIcon()}
        <div className="item-item-center">
          <div className="title-wrap">
            <div className="title">{title}</div>
            {subTitle?.show ? <div className="subTitle">{subTitle?.value}</div> : undefined}
          </div>
        </div>
      </>
    );
  };

  return (
    <div>
      <div className={prefixCls} {...rest} onClick={() => {
        if (serviceAddress){
          handler(serviceAddress);
        }else {
          handler(serviceName);
        }
      }}>
        {getContent()}
        <div className="iot-item-right">
          <div className="arrow"></div>
        </div>

        <div
          className="disabled-pangle"
          style={{ display: `${disabled ? 'block' : 'none'}` }}
        ></div>
      </div>
    </div>
  );
};

export default IOTEntry;
